<template>
<div>
    <div id="nav">
      <span></span>
      <i class="el-icon-star-off head_start"  @click="startgo" title="为我好评,感谢"></i>
      <i class="el-icon-s-tools head_start"   title="设置相关"></i>
      <i class="el-icon-top-right head_start"  @click="popgo" title="打开桌面"></i>
      <!-- <router-link to="/about">关于</router-link> -->
    </div>
  <div class="rate_list">
     <transition-group name="my-transition" tag="div">
    <div class="rate_item" v-for="(item) in rate_country_list" :key="item.tcur">
      <!-- <img :src="item.country_img" alt="" width="50px"> -->
      <img :src="'assets/flags/4x3/'+item.tcur.slice(0,2).toLowerCase()+'.svg'" alt="" width="50px" style="border-radius:5px;">
      <div class="rete_item_in">
        <el-input size="medium" v-model="item.num" placeholder="0" @input="oninput(item)">
          <template slot="prepend"><span style="color:rgb(102, 135, 242);font-size:23px;background-color:rgba(49,63,82,0);padding-left:0px"> {{item.currency_symbol}} </span></template>
        </el-input>
        <span style="font-size:16px;margin-left:35px">{{item.currency_name}}</span>
        <span style="font-size:12px;color:rgb(208, 217, 229);margin-left:35px">{{item.currency_status}}</span>
      </div>
      <i  @click="del_item(item)" style="place-self:start center;margin-top:10px;font-weight:900" class="el-icon-close item-close"></i>
    </div>
  </transition-group>

  <div class="add_country_button"><el-button type="warning" icon="el-icon-plus" circle @click="goto_add"></el-button></div>
  </div>
  </div>
</template>

<script>
// @ is an alias to /src  浅蓝rgb(49,63,82)   深蓝rgb(47,55,66)
export default {
  data(){
    return{
      rate_country_list:[],  //显示的国家货号列表
      country_list : []  //国家代码
    }
  },
  created(){
    this.resh_go();
  },
  methods:{
    del_item(item){
      let b = item.tcur
      let c = [] //新数组
      this.country_list.forEach(el=>{
        //console.log('当前国家代码:',el)
        if(el === b ){
        }else{
          c.push(el)
        }
      })
      this.country_list = c
    
      let self = this
      chrome.storage.local.set({ "my_rate_zz_countries": c }, function (s) {  
/*           let d = [] //新显示对象数组
          self.rate_country_list.forEach(el=>{
            if(el.tcur === item.tcur){
            }else{
              d.push(el)
            }
          })
          self.rate_country_list = d */
          let d = ''
          for(let i=0;i<self.rate_country_list.length;i++){
            if(self.rate_country_list[i].tcur === item.tcur){
              d = i
              break
            }
          }
          self.rate_country_list.splice(d,1)
        });
    },
    resh_go(){
    let self = this
    chrome.storage.local.get(["my_rate_zz",'my_rate_zz_countries'],function(result){
        //console.log("从storage中取出来的汇率数据为:");
        //console.log(result);
        let {my_rate_zz,my_rate_zz_countries} = result
        if(!my_rate_zz_countries){
          my_rate_zz_countries = ['CNY','USD']
          chrome.storage.local.set({ "my_rate_zz_countries": my_rate_zz_countries }, function (s) {  
              console.log("初始化国家列表");
    })
        }
        self.country_list = my_rate_zz_countries
        let c = []
        self.country_list.forEach(el => {
            let rate_country = {}
            rate_country.country_img = my_rate_zz[el].flagURL
            rate_country.currency_symbol = my_rate_zz[el].symbol
            rate_country.num = ''
            rate_country.currency_name = my_rate_zz[el].name
            rate_country.currency_status= ''
            rate_country.tcur = my_rate_zz[el].tcur
            rate_country.rate = my_rate_zz[el].rate
            //console.log(rate_country)
            c.push(rate_country)
        });
        self.rate_country_list = c
    });
    },
    popgo(){
           
      var myWidth = 405;
      var myHeight = 580;
      var left = (screen.width - myWidth) / 1.05;
      var top = 60;
      window.open(location.href, "popUpWindow",'scrollbars=no,resizable=no,titlebar=no,toolbar=no,menubar=no,location=no,directories=no,status=yes,width=' + myWidth + ',height=' + myHeight + ',top=' + top + ', left=' + left);
        // window.close();
      
    },
    startgo(){
      window.open('https://chrome.google.com/webstore/detail/%E6%B1%87%E7%8E%87%E8%BD%AC%E6%8D%A2/bcpgdpedphodjcjlminjbdeejccjbimp/reviews')
    },
    oninput(val){
      //console.log(val)
      let a = []
      let rate_now = Number(val.rate)
      this.rate_country_list.forEach(el=>{
        if(val.tcur === el.tcur){
            el.currency_status = `1 ${el.tcur} = 1 ${el.tcur}`
            a.push(el)
        }else{
            el.num = ((Number(el.rate) / Number(val.rate)) * Number(val.num)).toFixed(4)
            el.currency_status = `1 ${val.tcur} = ${(Number(el.rate) / Number(val.rate)).toFixed(4)} ${el.tcur}`
        }
      })
    },
    goto_add(){
      this.$router.push('/add_list')
    }
  },

}
</script>
